const onStyle = {
  "background-color": "blue"
};
const steps = step => {
  const steps = [...Array(8)];
  return steps.map((_, i) => {
    return (
      <div className={"column " + (step == i ? "on" : "off")}>
        <input type="checkbox" />
      </div>
    );
  });
};

const Track = props => <div className="columns">{steps()}</div>;

export default Track;
